*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    -webkit-tap-highlight-color: transparent;
}

*:focus {
    outline: 0;
}

::-moz-selection { /* Code for Firefox */
    background: lighten($color-primary, 37);
}

::selection {
    background: lighten($color-primary, 37);
}

html {
    max-width: 100vw;
    height: 100%;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    font-family: $text-ff;
    font-size: $text-fz; // only used for rem-handling, the actual font-size is $body__fontsize
    line-height: $text-lh;
    color: $text-c;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    // used as wrapper for sticky footer
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    background-color: $html__bgcolor !important;
}

main {
    position: relative;
    margin-top: $header_height;

    article,
    .element {
        padding: 1rem 0;
    }
}

aside {
    position: relative;
    margin-top: $header_height;

    .widget {
        padding: 1rem 0;
        border-bottom: 1px solid $color-grey;

        .nav-link {
            padding: 0 0 0.5rem 0;
        }
    }
}

article,
.element,
nav,
footer .inner {
    // width: $element_width;
    //  max-width: $element_maxwidth;
    margin: 0 auto;

    @include breakpoint(mobile) {
        width: $element_width_mobile;
    }
}

.container {
    width: 100%;
    margin: 0 auto;

    @include breakpoint(tablet) {
        width: 720px;
    }


    @include breakpoint(laptop) {
        width: 960px;
    }


    @include breakpoint(desktop) {
        width: 1140px;
    }
}

// Background colors
.bg-primary {
    background-color: $color-primary;
}

.bg-light {
    background-color: $color-light;
}

.bg-white {
    background-color: #fff;
}

// Grid

.d-flex {
    display: flex;

    @include breakpoint(mobile) {
        flex-wrap: wrap;
    }
}

.flex-gap {
    display: inline-flex;
    gap: 12px;

    @include breakpoint(mobile) {
        flex-wrap: wrap;
    }
}

.d-grid {
    display: grid;
}
